import React from 'react'
import * as chroma from 'chroma-js'
import { scaleRed, scaleBlue } from './Static'

class Bar extends React.Component {
  render() {
    let { grem, analyze, setAnalyze, combined } = this.props

    let counts = [
      combined.filter(r => r.class === 'neg').length,
      combined.filter(r => r.class === 'pos').length,
    ]

    return (
      <div
        style={{
          // background: analyze ? chroma('orchid').luminance(0.25) : 'white',
          // color: 'white',
          background: 'white',
          borderTop: 'solid 1px black',
          position: 'fixed',
          left: 0,
          bottom: 0,
          right: 0,
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        {analyze ? (
          <div
            style={{
              position: 'absolute',
              left: 0,
              top: 0,
              right: 0,
              bottom: 0,
              mixBlendMode: 'multiply',
            }}
          >
            {combined.map((c, i) => (
              <div
                style={{
                  position: 'absolute',
                  left: `${(1 / combined.length) * i * 100}%`,
                  top: 0,
                  bottom: 0,
                  width: `${(1 / combined.length) * 100 + 0.05}%`,
                  background:
                    c.class === 'pos'
                      ? scaleBlue(c.certainty)
                      : scaleRed(c.certainty),
                }}
              />
            ))}
          </div>
        ) : null}

        <div style={{ padding: grem / 2 }}>
          {combined.length} reviews
          {analyze ? (
            <span>
              {' '}
              &middot;{' '}
              {counts[1] > counts[0] ? (
                <span>
                  {Math.round((counts[1] / combined.length) * 100)}% positive
                </span>
              ) : (
                <span>
                  {Math.round((counts[0] / combined.length) * 100)}% negative
                </span>
              )}
            </span>
          ) : null}
        </div>
        <div style={{ display: 'flex' }}>
          <div>
            {!analyze ? (
              <button
                onClick={() => {
                  setAnalyze(true)
                }}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  paddingRight: grem / 2,
                  position: 'relative',
                  padding: grem / 2,
                }}
              >
                <div style={{ marginRight: grem / 2 }}>Analysis: </div>
                <div
                  style={{
                    background: 'white',
                    height: grem,
                    width: grem * 1.75,
                    overflow: 'hidden',
                    borderRadius: grem / 2,
                    border: 'solid 1px black',
                    marginRight: grem / 2,
                    position: 'relative',
                  }}
                >
                  <div
                    style={{
                      width: grem * 1,
                      height: grem,
                      background: '#222',
                      borderRadius: '50%',
                      position: 'absolute',
                      left: -1,
                    }}
                  />
                </div>
                Off
              </button>
            ) : (
              <button
                onClick={() => {
                  setAnalyze(false)
                }}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  paddingRight: grem / 2,
                  position: 'relative',
                  padding: grem / 2,
                }}
              >
                <div style={{ marginRight: grem / 2 }}>Analysis: </div>
                <div
                  style={{
                    background: 'white',
                    height: grem,
                    width: grem * 1.75,
                    overflow: 'hidden',
                    borderRadius: grem / 2,
                    border: 'solid 1px black',
                    marginRight: grem / 2,
                    position: 'relative',
                  }}
                >
                  <div
                    style={{
                      width: grem * 1,
                      height: grem,
                      background: '#222',
                      borderRadius: '50%',
                      position: 'absolute',
                      right: -1,
                    }}
                  />
                </div>
                On
              </button>
            )}
          </div>
        </div>
      </div>
    )
  }
}
export default Bar
